"use client";

import { useState, useEffect } from "react";
import { useParams, useRouter } from "next/navigation";
import ChatBox from "../../../components/ChatBox";
import { Agent } from "../../../components/AgentCreationModal";

export default function AgentChatPage() {
  const params = useParams();
  const router = useRouter();
  const [agent, setAgent] = useState<Agent | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function loadAgent() {
      try {
        const storedAgents = localStorage.getItem("agents");
        if (!storedAgents) {
          setError("No agents found");
          return;
        }

        const agents: Agent[] = JSON.parse(storedAgents);
        const foundAgent = agents.find((a) => a.id === params.agentId);
        
        if (!foundAgent) {
          setError("Agent not found");
          return;
        }

        setAgent(foundAgent);
      } catch (err) {
        console.error("Error loading agent:", err);
        setError("Failed to load agent data");
      } finally {
        setIsLoading(false);
      }
    }

    if (params.agentId) {
      loadAgent();
    }
  }, [params.agentId]);

  function handleBackToAgents() {
    router.push("/agents");
  }

  function formatDate(dateString: string) {
    try {
      return new Date(dateString).toLocaleDateString();
    } catch {
      return "Unknown";
    }
  }

  if (isLoading) {
    return (
      <div className="h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="animate-spin w-8 h-8 border-2 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading agent...</p>
        </div>
      </div>
    );
  }

  if (error || !agent) {
    return (
      <div className="h-screen flex items-center justify-center bg-background">
        <div className="text-center max-w-md mx-auto p-6">
          <div className="w-16 h-16 bg-muted rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-2xl">⚠️</span>
          </div>
          <h2 className="text-xl font-semibold mb-2">
            {error === "Agent not found" ? "Agent Not Found" : "Error Loading Agent"}
          </h2>
          <p className="text-muted-foreground mb-6">
            {error === "Agent not found" 
              ? "The agent you're looking for doesn't exist or may have been deleted."
              : "There was a problem loading the agent data."
            }
          </p>
          <button
            onClick={handleBackToAgents}
            className="px-6 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors font-medium"
          >
            Back to Agents
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="h-screen flex flex-col bg-background">
      {/* Header */}
      <header className="border-b border-border bg-card">
        <div className="flex items-center justify-between p-4">
          <div className="flex items-center space-x-4">
            <button
              onClick={handleBackToAgents}
              className="text-muted-foreground hover:text-foreground transition-colors"
              title="Back to agents"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
            <div>
              <h1 className="text-xl font-bold">{agent.name}</h1>
              <p className="text-sm text-muted-foreground">
                Created {formatDate(agent.createdAt)}
              </p>
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={handleBackToAgents}
              className="px-4 py-2 text-sm border border-input rounded-md hover:bg-accent hover:text-accent-foreground transition-colors"
            >
              All Agents
            </button>
          </div>
        </div>
        
        {/* System Prompt Preview */}
        <div className="px-4 pb-4">
          <details className="group">
            <summary className="cursor-pointer text-sm text-muted-foreground hover:text-foreground transition-colors">
              <span className="group-open:hidden">Show system prompt</span>
              <span className="hidden group-open:inline">Hide system prompt</span>
            </summary>
            <div className="mt-2 p-3 bg-muted rounded-md text-sm">
              <p className="whitespace-pre-wrap">{agent.systemPrompt}</p>
            </div>
          </details>
        </div>
      </header>

      {/* Chat Area */}
      <main className="flex-1 overflow-hidden">
        <ChatBox systemPrompt={agent.systemPrompt} />
      </main>
    </div>
  );
}
